.contact-form {
    display: none;
    .substrate {
        background-color: rgba(0, 0, 0, .4);
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .form {
        background-color: white;
        width: 300px;
        height: 500px;
        opacity: 1;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        // position: absolute;
        // z-index: 9999;

        &__title {
            font-size: 25px;
            font-weight: 600;
            text-align: center;
        }

        &__inputs {
            margin-top: 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 60px;
            width: 100%;
        }

        &__input {
            // border-radius: 5px;
            // border-color: #33333380;
            border: none;
            border-bottom: 1px solid rgba(51, 51, 51, 0.50);
            width: 100%;
        }

        &__btn {
            margin-top: 53px;
            width: 288px;
            height: 52px;
            display: flex;
            padding: 16px 32px;
            justify-content: center;
            align-items: center;
            border-radius: 100px;
            border: 1px solid var(--orange, #FF5900);
            color: var(--orange, #FF5900);
            font-size: 16px;
            font-weight: 500;
            cursor: pointer;
        }

        &__btn:hover {
            outline: none;
            box-shadow: 0 0 0 4px #cbd6ee;
        }

        &__checkbox {
            margin-top: 20px;
            display: flex;
            gap: 5px;
            align-items:flex-start;
        }

        &__checkbox-label {
            color: var(--typography, #212121);
            font-size: 14px;
            font-weight: 400;
            line-height: normal;
        }
    }
}

.contact-form_active {
    display: block;
}
