.sertification {
    &__title {
        margin-top: 120px;
        color: var(--typography, #212121);
        font-size: 38px;
        font-weight: 700;
    }
}

.benefits {
    margin-top: 60px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.benefit {
    width: 570px;

    &__picture {
        img {
            object-fit: contain;
        }
    }

    &__wrapper {
        padding: 32px 57px 40px 20px;
    }

    &__title {
        color: var(--typography, #212121);
        font-size: 18px;
        font-weight: 600;
        letter-spacing: 0.18px;
        text-transform: uppercase;
    }

    &__descr {
        margin-top: 16px;
        color: var(--typography, #212121);
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
        ul {
            margin-top: 10px;
        }
        li {
            margin-top: 5px;
        }
    }

    &__descr-title {
        color: var(--typography, #212121);
        font-size: 16px;
        font-weight: 500;
    }
}
