.header {
    &__up {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 26px 0;
    }
    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #212121;
        opacity: 0.7;
    }
    &__contacts {
        width: 410px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__phone {
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #212121;
        opacity: 0.7;
        text-decoration: none;
    }
    &__phone:hover {
        color: var(--orange, #FF5900);
    }
}
.btn {
    width: 222px;
    height: 52px;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &_header {
        border: 1px solid #FF5900;
        background-color: transparent;
        height: 36px;
    }
    &__text {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #FF5900;
        margin-left: 8px;
    }
}
.btn:hover {
    outline: none;
    box-shadow: 0 0 0 4px #cbd6ee;
}
.hamburger {
    display: none;
    width: 30px;
    height: 30px;
    cursor: pointer;
    span {
        transition: all .5s;
        display: block;
        width: 100%;
        height: 3px;
        background-color: #212121;
        margin-bottom: 5px;
    }
}
.navigation {
    padding: 37px 0;
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__dropdown {
        visibility: hidden;
        opacity: 0;
        transition-property: visibility, opacity;
        transition-duration: 0.2s;
        transition-delay: 0.2s;
        position: absolute;
        width: 352px;
        padding-top: 37px !important;
        .navigation__item {
            width: 100%;
            color: red;
            padding: 16px;
            border-top: 1px solid rgba(51, 51, 51, 0.1);
            &_withlabel {
                display: flex;
                justify-content: space-between;
            }
            &-link {
                color: rgba(33, 33, 33, 0.5);
            }
            &:last-child {
                border-bottom: 1px solid rgba(51, 51, 51, 0.1);
            }
        }
    }
    &__label {
        display: inline-block;
        width: 36px;
        height: 16px;
        position: relative;
        img {
            width: 100%;
            height: 100%;
        }
        span {
            font-weight: 700;
            font-size: 10px;
            line-height: 110%;
            color: #FFFFFF;
            position: absolute;
            right: 4px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    &__item {
        list-style: none;
        font-weight: 500;
        font-size: 14px;
        line-height: 120%;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        &:hover .navigation__dropdown {
            visibility: visible;
            opacity: 1;
            transition-delay: 0.3s;
            padding-top: 37px;
            background-color: #FFFFFF;
        }
        &-link {
            text-decoration: none;
            color: #212121;
            display: flex;
            justify-content: space-between;
            align-items: center;
            img {
                margin-left: 4px;
            }
            &:hover {
                color: #FF5900;
            }
        }
    }
}

.line {
    width: 100%;
    height: 1px;
    background-color: rgba(51, 51, 51, 0.1);
    border: none;
    margin: 0;
}
