.promo {
    padding: 170px 0 210px;
    // background-image: url('../img/promo_bg.jpg');
    background-position: center;
    background-size: cover;
    &__title {
        width: 784px;
        font-weight: 700;
        font-size: 68px;
        line-height: 110%;
        color: #FFFFFF;
    }
    &__descr {
        margin-top: 28px;
        width: 690px;
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: #FFFFFF;
    }
    &__wrapper {
        margin-top: 52px;
        display: flex;
        gap: 32px;
    }
    &__btn {
        width: 341px;
        height: 62px;
        background: rgba(255, 255, 255, 0.2);
        border: 1px solid #FFFFFF;
        backdrop-filter: blur(2.5px);
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
        &_orange {
            background: #FF5900;
            border-color: #FF5900;
        }
    }
}
