.footer {
    background-color: #333333;
    padding-top: 100px;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        color: rgba(255, 255, 255, .5);
        font-size: 14px;
        font-weight: 500;
        padding-bottom: 80px;
        border-bottom: 1px solid rgba(255, 255, 255, .1);
    }
    &__region {
        margin-top: 57px;
        display: flex;
        align-items: center;
        gap: 4px;
        &-text {
            font-weight: 500;
            font-size: 14px;
            line-height: 170%;
        }
        &-city {
            font-weight: 500;
            font-size: 14px;
            line-height: 100%;
            color: #FFFFFF;
            text-decoration: underline;
        }
    }
    &__phone {
        display: block;
        margin-top: 20px;
        line-height: 170%;
        color: #FFFFFF;
    }
    &__address {
        font-size: 14px;
        line-height: 170%;
        color: #FFFFFF;
    }
    &__inn {
        margin-top: 12px;
        line-height: 170%;
    }
    &__ogrn {
        line-height: 170%;
    }
    &__icons {
        margin-top: 28px;
        display: flex;
        gap: 20px;
    }
    &__lists {
        display: flex;
        justify-content: flex-end;
        gap: 130px;
        &-left {
            width: 190px;
        }
        &-center {
            width: 190px;
        }
        &-right {
            width: 190px;
        }
    }
    &__list {
        margin-top: 24px;
        &-link {
            color: #FFFFFF;
            line-height: 170%;
        }
    }
    &__underground {
        padding: 20px 0;
        display: flex;
        justify-content: space-between;
        color: rgba(255, 255, 255, .3);
        font-weight: 500;
        font-size: 12px;
        line-height: 120%;
        &-left {
            a {
                color: rgba(255, 255, 255, .3);
            }
        }
        &-map {
            color: rgba(255, 255, 255, .3);
        }
        &-policy {
            color: rgba(255, 255, 255, .3);
        }
    }
}
