.steps {
    font-family: Montserrat;

    &__title {
        margin-top: 120px;
        color: var(--typography, #212121);
        font-size: 38px;
        font-weight: 700;
        text-align: center;
        span {
            color: var(--orange, #FF5900);
        }
    }

    &__wrapper {
        margin-top: 64px;
        display: flex;
        justify-content: space-between;
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }

    &__circle {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        background-color: var(--gray-light, #F4F4F4);
        border-radius: 100%;
    }

    &__number {
        color: var(--typography, #212121);
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        letter-spacing: 0.18px;
        text-transform: uppercase;
        opacity: .3;
    }

    &__descr {
        margin-top: 20px;
        text-align: center;
    }
}
