@media (max-width: 1200px) {
    .container {
        width: 960px;
    }

    .header {
        &__text {
            display: none;
        }
    }

    .sertification {
        .benefit {
            width: 470px;
        }
    }

    .contacts {
        .contact {
            gap: 10px;
        }
        .map {
            iframe {
                width: 400px;
                height: 400px;
            }
        }
    }

    .footer {
        &__lists {
            gap: 30px;
        }
    }
}

@media (max-width: 992px) {
    .container {
        width: 740px;
    }

    .header {
        &__contacts {
            flex-wrap: wrap;
            justify-content: flex-end;
            gap: 10px;
        }
        &__phone {
            width: 160px;
        }
    }

    .promo {
        &__title {
            width: 100%;
            font-size: 64px;
        }
    }

    .navigation {
        &__item {
            font-size: 10px;
        }
    }

    .steps {
        &__wrapper {
            flex-wrap: wrap;
            width: 200px;
            justify-content: center;
            margin: 0 auto;
            gap: 20px;
        }
        &__item {
            gap: 5px;
        }
        &__descr {
            width: 150px;
            margin-top: 0;
        }
    }

    .benefits {
        justify-content: center;
    }

    .advantages {
        &__items {
            margin: 50px 0 50px;
            justify-content: space-around;
            flex-wrap: wrap;
        }
    }

    .services {

        .offers {
            gap: 15px;
            justify-content: center;
        }
    }

    .contacts {
        .contact {
            flex-wrap: wrap;
            &__left {
                width: 100%;
            }
            &__right {
                .map {
                    iframe {
                        width: 740px;
                    }
                }
            }
        }

    }

    .footer {
        padding-top: 30px;

        &__wrapper {
            flex-direction: column;
            align-items: center;
            gap: 50px;
        }

        &__contacts {
            order: 1;
            text-align: center;
        }

        &__region {
            margin-top: 20px;
            justify-content: center;
        }

        &__lists {
            gap: 0;
            justify-content: space-between;
        }

        &__icons {
            justify-content: center;
        }
    }
}

@media (max-width: 768px) {
    .container {
        width: 540px;
    }

    .header {
        &__phone {
            text-align: end;
        }
    }

    .btn {
        &_header {
            width: 180px;
        }

        &__text {
            font-size: 13px;
        }
    }

    .promo {
        &__title {
            font-size: 45px;
        }

        &__descr {
            width: 100%;
        }
    }

    .navigation {
        padding: 0;
        position: relative;

        &__list {
            flex-direction: column;
            align-items: flex-start;
            transition: all .5s;
            transform: translateX(-220%);
            position: absolute;
            background-color: #FFFFFF;

            &_active {
                transition: all .5s;
                transform: translateX(0);
                display: flex;
                z-index: 999;
                position: absolute;
                padding: 5px !important;
            }
        }

        &__dropdown {
            background-color: #FFFFFF;
            top: 0;
            left: 150px;
            width: 200px;
        }

        &__item {
            &-search {
                display: none;
            }
        }

        .navigation__item {
            padding: 4px;
        }
    }

    .hamburger {
        display: block;
        margin-top: 10px;
    }

    .hamburger__active {
        position: relative;

        span {
            position: absolute;
            top: 10px;
            transition: all .5s;

            &:nth-child(2) {
                transform: rotate(-45deg);
            }

            &:nth-child(3) {
                transform: rotate(45deg);
            }

            &:nth-child(1) {
                display: none;
            }
        }
    }

    .contacts {
        .contact {
            &__left {
                width: 100%;
            }
            &__right {
                .map {
                    iframe {
                        width: 540px;
                    }
                }
            }
        }
    }

    .footer {
        &__wrapper {
            gap: 30px;
            padding-bottom: 30px;
        }

        &__lists {
            gap: 5px;

            &-left {
                width: 180px;
            }

            &-center {
                width: 180px;
            }

            &-right {
                width: 180px;
            }
        }

        &__underground {
            font-size: 11px;
        }
    }
}

@media (max-width: 576px) {
    .container {
        width: 400px;
    }

    .header {
        &__up {
            flex-direction: column;
            gap: 20px;
        }

        &__contacts {
            width: 100%;
        }
    }

    .btn {
        &_header {
            width: 154px;
        }
    }

    .navigation {
        &__dropdown {
            width: 150px;
        }
    }

    .promo {
        &__title {
            font-size: 32px;
        }

        &__descr {
            font-size: 13px;
        }

        &__wrapper {
            flex-direction: column;
            align-items: center;
        }

        &__btn {
            width: 90%;
        }
    }

    .advantages {
        &__items {
            gap: 30px;
        }
    }

    .steps {
        &__title {
            margin-top: 50px;
            font-size: 25px;
            span {
                display: block;
            }
        }
        &__wrapper {
            margin-top: 20px;
        }
    }

    .sertification {
        &__title {
            font-size: 25px;
        }
        .benefits {
            gap: 20px;
            .benefit {
                width: 290px;
                &__wrapper {
                    padding: 15px 0 0 0;
                }
            }
        }
    }

    .contacts {
        .contact {
            gap: 20px;
            &__left-wrapper {
                flex-wrap: wrap;
                gap: 30px;
            }
            &__right {
                .map {
                    iframe {
                        width: 300px;
                    }
                }
            }
        }
    }

    .footer {
        &__lists {
            flex-direction: column;
            gap: 20px;
        }

        &__list {
            margin-top: 0;
        }

        &__underground {
            flex-direction: column;
            align-items: center;
            gap: 10px;
        }
    }
}
