html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.eot");
  src: url("../fonts/Montserrat-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.eot");
  src: url("../fonts/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.eot");
  src: url("../fonts/Montserrat-SemiBold.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-ExtraBold.eot");
  src: url("../fonts/Montserrat-ExtraBold.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-ExtraBold.woff") format("woff"), url("../fonts/Montserrat-ExtraBold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
body {
  font-family: "Montserrat";
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header__up {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 26px 0;
}
.header__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #212121;
  opacity: 0.7;
}
.header__contacts {
  width: 410px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__phone {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #212121;
  opacity: 0.7;
  text-decoration: none;
}
.header__phone:hover {
  color: var(--orange, #FF5900);
}

.btn {
  width: 222px;
  height: 52px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.btn_header {
  border: 1px solid #FF5900;
  background-color: transparent;
  height: 36px;
}
.btn__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #FF5900;
  margin-left: 8px;
}

.btn:hover {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}

.hamburger {
  display: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.hamburger span {
  transition: all 0.5s;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #212121;
  margin-bottom: 5px;
}

.navigation {
  padding: 37px 0;
}
.navigation__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation__dropdown {
  visibility: hidden;
  opacity: 0;
  transition-property: visibility, opacity;
  transition-duration: 0.2s;
  transition-delay: 0.2s;
  position: absolute;
  width: 352px;
  padding-top: 37px !important;
}
.navigation__dropdown .navigation__item {
  width: 100%;
  color: red;
  padding: 16px;
  border-top: 1px solid rgba(51, 51, 51, 0.1);
}
.navigation__dropdown .navigation__item_withlabel {
  display: flex;
  justify-content: space-between;
}
.navigation__dropdown .navigation__item-link {
  color: rgba(33, 33, 33, 0.5);
}
.navigation__dropdown .navigation__item:last-child {
  border-bottom: 1px solid rgba(51, 51, 51, 0.1);
}
.navigation__label {
  display: inline-block;
  width: 36px;
  height: 16px;
  position: relative;
}
.navigation__label img {
  width: 100%;
  height: 100%;
}
.navigation__label span {
  font-weight: 700;
  font-size: 10px;
  line-height: 110%;
  color: #FFFFFF;
  position: absolute;
  right: 4px;
  top: 50%;
  transform: translateY(-50%);
}
.navigation__item {
  list-style: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
.navigation__item:hover .navigation__dropdown {
  visibility: visible;
  opacity: 1;
  transition-delay: 0.3s;
  padding-top: 37px;
  background-color: #FFFFFF;
}
.navigation__item-link {
  text-decoration: none;
  color: #212121;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation__item-link img {
  margin-left: 4px;
}
.navigation__item-link:hover {
  color: #FF5900;
}

.line {
  width: 100%;
  height: 1px;
  background-color: rgba(51, 51, 51, 0.1);
  border: none;
  margin: 0;
}

.promo {
  padding: 170px 0 210px;
  background-position: center;
  background-size: cover;
}
.promo__title {
  width: 784px;
  font-weight: 700;
  font-size: 68px;
  line-height: 110%;
  color: #FFFFFF;
}
.promo__descr {
  margin-top: 28px;
  width: 690px;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #FFFFFF;
}
.promo__wrapper {
  margin-top: 52px;
  display: flex;
  gap: 32px;
}
.promo__btn {
  width: 341px;
  height: 62px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid #FFFFFF;
  backdrop-filter: blur(2.5px);
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
}
.promo__btn_orange {
  background: #FF5900;
  border-color: #FF5900;
}

.advantages {
  margin-top: 120px;
}
.advantages__title {
  font-size: 38px;
  font-weight: 700;
}
.advantages__items {
  margin: 50px 0 120px 0;
  display: flex;
  justify-content: space-between;
}
.advantages__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.advantages__item-img {
  width: 70px;
  height: 70px;
}
.advantages__item-text {
  width: 250px;
  color: var(--gray, #333);
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
}

.strip {
  height: 1px;
  background: var(--gray, #333);
}

.services__title {
  margin-top: 120px;
  color: var(--typography, #212121);
  font-size: 38px;
  font-weight: 700;
}
.services__descr {
  margin-top: 40px;
  max-width: 671px;
}

.offers {
  margin: 60px 0 60px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.offer {
  background: var(--gray-light, #F4F4F4);
  width: 360px;
  height: 449px;
  padding: 32px 24px 40px 24px;
}
.offer__title {
  color: var(--typography, #212121);
  font-size: 18px;
  font-weight: 700;
  line-height: 140%;
  letter-spacing: 0.18px;
  text-transform: uppercase;
}
.offer__strip {
  width: 312px;
  height: 1px;
  opacity: 0.2;
  background: var(--gray, #333);
}
.offer__descr {
  height: 80px;
  margin-top: 41px;
  color: var(--typography, #212121);
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  opacity: 0.5;
}
.offer__subdescr {
  margin-top: 60px;
  color: var(--typography, #212121);
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  opacity: 0.5;
}
.offer__price {
  margin-top: 20px;
  color: var(--orange, #FF5900);
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
}
.offer__price span {
  font-size: 18px;
  font-weight: 600;
}
.offer__btn {
  margin-top: 53px;
  width: 312px;
  height: 52px;
  display: flex;
  width: 312px;
  padding: 16px 32px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 1px solid var(--orange, #FF5900);
  color: var(--orange, #FF5900);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
.offer__btn:hover {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}

.footer {
  background-color: #333333;
  padding-top: 100px;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  color: rgba(255, 255, 255, 0.5);
  font-size: 14px;
  font-weight: 500;
  padding-bottom: 80px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.footer__region {
  margin-top: 57px;
  display: flex;
  align-items: center;
  gap: 4px;
}
.footer__region-text {
  font-weight: 500;
  font-size: 14px;
  line-height: 170%;
}
.footer__region-city {
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: #FFFFFF;
  text-decoration: underline;
}
.footer__phone {
  display: block;
  margin-top: 20px;
  line-height: 170%;
  color: #FFFFFF;
}
.footer__address {
  font-size: 14px;
  line-height: 170%;
  color: #FFFFFF;
}
.footer__inn {
  margin-top: 12px;
  line-height: 170%;
}
.footer__ogrn {
  line-height: 170%;
}
.footer__icons {
  margin-top: 28px;
  display: flex;
  gap: 20px;
}
.footer__lists {
  display: flex;
  justify-content: flex-end;
  gap: 130px;
}
.footer__lists-left {
  width: 190px;
}
.footer__lists-center {
  width: 190px;
}
.footer__lists-right {
  width: 190px;
}
.footer__list {
  margin-top: 24px;
}
.footer__list-link {
  color: #FFFFFF;
  line-height: 170%;
}
.footer__underground {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  color: rgba(255, 255, 255, 0.3);
  font-weight: 500;
  font-size: 12px;
  line-height: 120%;
}
.footer__underground-left a {
  color: rgba(255, 255, 255, 0.3);
}
.footer__underground-map {
  color: rgba(255, 255, 255, 0.3);
}
.footer__underground-policy {
  color: rgba(255, 255, 255, 0.3);
}

.contact-form {
  display: none;
}
.contact-form .substrate {
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contact-form .form {
  background-color: white;
  width: 300px;
  height: 500px;
  opacity: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-form .form__title {
  font-size: 25px;
  font-weight: 600;
  text-align: center;
}
.contact-form .form__inputs {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 60px;
  width: 100%;
}
.contact-form .form__input {
  border: none;
  border-bottom: 1px solid rgba(51, 51, 51, 0.5);
  width: 100%;
}
.contact-form .form__btn {
  margin-top: 53px;
  width: 288px;
  height: 52px;
  display: flex;
  padding: 16px 32px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 1px solid var(--orange, #FF5900);
  color: var(--orange, #FF5900);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}
.contact-form .form__btn:hover {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}
.contact-form .form__checkbox {
  margin-top: 20px;
  display: flex;
  gap: 5px;
  align-items: flex-start;
}
.contact-form .form__checkbox-label {
  color: var(--typography, #212121);
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.contact-form_active {
  display: block;
}

.sertification__title {
  margin-top: 120px;
  color: var(--typography, #212121);
  font-size: 38px;
  font-weight: 700;
}

.benefits {
  margin-top: 60px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.benefit {
  width: 570px;
}
.benefit__picture img {
  object-fit: contain;
}
.benefit__wrapper {
  padding: 32px 57px 40px 20px;
}
.benefit__title {
  color: var(--typography, #212121);
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.18px;
  text-transform: uppercase;
}
.benefit__descr {
  margin-top: 16px;
  color: var(--typography, #212121);
  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
}
.benefit__descr ul {
  margin-top: 10px;
}
.benefit__descr li {
  margin-top: 5px;
}
.benefit__descr-title {
  color: var(--typography, #212121);
  font-size: 16px;
  font-weight: 500;
}

.steps {
  font-family: Montserrat;
}
.steps__title {
  margin-top: 120px;
  color: var(--typography, #212121);
  font-size: 38px;
  font-weight: 700;
  text-align: center;
}
.steps__title span {
  color: var(--orange, #FF5900);
}
.steps__wrapper {
  margin-top: 64px;
  display: flex;
  justify-content: space-between;
}
.steps__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.steps__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  background-color: var(--gray-light, #F4F4F4);
  border-radius: 100%;
}
.steps__number {
  color: var(--typography, #212121);
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: 0.18px;
  text-transform: uppercase;
  opacity: 0.3;
}
.steps__descr {
  margin-top: 20px;
  text-align: center;
}

.contacts__wrapper {
  margin: 30px 0 30px 0;
}

.contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--typography, #212121);
  font-size: 16px;
  font-weight: 500;
}
.contact__left {
  width: 560px;
}
.contact__title {
  color: var(--typography, #212121);
  font-size: 38px;
  font-weight: 700;
}
.contact__descr {
  margin-top: 12px;
}
.contact__left-wrapper {
  margin-top: 38px;
  display: flex;
  justify-content: space-between;
}
.contact__adress-title {
  color: var(--typography, #212121);
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.18px;
  text-transform: uppercase;
  opacity: 0.2;
}
.contact__adress-descr {
  margin-top: 8px;
  width: 300px;
}
.contact__regime {
  margin-top: 28px;
}
.contact__regime-title {
  color: var(--typography, #212121);
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.18px;
  text-transform: uppercase;
  opacity: 0.2;
}
.contact__regime-descr {
  margin-top: 8px;
}
.contact__social-title {
  color: var(--typography, #212121);
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.18px;
  text-transform: uppercase;
  opacity: 0.2;
}
.contact__social-phone {
  margin-top: 8px;
}
.contact__icons {
  margin-top: 28px;
  display: flex;
}
.contact__icons-item:nth-child(2) {
  margin-left: 20px;
}

.map {
  max-width: 600px;
  max-height: 600px;
}

@media (max-width: 1200px) {
  .container {
    width: 960px;
  }
  .header__text {
    display: none;
  }
  .sertification .benefit {
    width: 470px;
  }
  .contacts .contact {
    gap: 10px;
  }
  .contacts .map iframe {
    width: 400px;
    height: 400px;
  }
  .footer__lists {
    gap: 30px;
  }
}
@media (max-width: 992px) {
  .container {
    width: 740px;
  }
  .header__contacts {
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 10px;
  }
  .header__phone {
    width: 160px;
  }
  .promo__title {
    width: 100%;
    font-size: 64px;
  }
  .navigation__item {
    font-size: 10px;
  }
  .steps__wrapper {
    flex-wrap: wrap;
    width: 200px;
    justify-content: center;
    margin: 0 auto;
    gap: 20px;
  }
  .steps__item {
    gap: 5px;
  }
  .steps__descr {
    width: 150px;
    margin-top: 0;
  }
  .benefits {
    justify-content: center;
  }
  .advantages__items {
    margin: 50px 0 50px;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .services .offers {
    gap: 15px;
    justify-content: center;
  }
  .contacts .contact {
    flex-wrap: wrap;
  }
  .contacts .contact__left {
    width: 100%;
  }
  .contacts .contact__right .map iframe {
    width: 740px;
  }
  .footer {
    padding-top: 30px;
  }
  .footer__wrapper {
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }
  .footer__contacts {
    order: 1;
    text-align: center;
  }
  .footer__region {
    margin-top: 20px;
    justify-content: center;
  }
  .footer__lists {
    gap: 0;
    justify-content: space-between;
  }
  .footer__icons {
    justify-content: center;
  }
}
@media (max-width: 768px) {
  .container {
    width: 540px;
  }
  .header__phone {
    text-align: end;
  }
  .btn_header {
    width: 180px;
  }
  .btn__text {
    font-size: 13px;
  }
  .promo__title {
    font-size: 45px;
  }
  .promo__descr {
    width: 100%;
  }
  .navigation {
    padding: 0;
    position: relative;
  }
  .navigation__list {
    flex-direction: column;
    align-items: flex-start;
    transition: all 0.5s;
    transform: translateX(-220%);
    position: absolute;
    background-color: #FFFFFF;
  }
  .navigation__list_active {
    transition: all 0.5s;
    transform: translateX(0);
    display: flex;
    z-index: 999;
    position: absolute;
    padding: 5px !important;
  }
  .navigation__dropdown {
    background-color: #FFFFFF;
    top: 0;
    left: 150px;
    width: 200px;
  }
  .navigation__item-search {
    display: none;
  }
  .navigation .navigation__item {
    padding: 4px;
  }
  .hamburger {
    display: block;
    margin-top: 10px;
  }
  .hamburger__active {
    position: relative;
  }
  .hamburger__active span {
    position: absolute;
    top: 10px;
    transition: all 0.5s;
  }
  .hamburger__active span:nth-child(2) {
    transform: rotate(-45deg);
  }
  .hamburger__active span:nth-child(3) {
    transform: rotate(45deg);
  }
  .hamburger__active span:nth-child(1) {
    display: none;
  }
  .contacts .contact__left {
    width: 100%;
  }
  .contacts .contact__right .map iframe {
    width: 540px;
  }
  .footer__wrapper {
    gap: 30px;
    padding-bottom: 30px;
  }
  .footer__lists {
    gap: 5px;
  }
  .footer__lists-left {
    width: 180px;
  }
  .footer__lists-center {
    width: 180px;
  }
  .footer__lists-right {
    width: 180px;
  }
  .footer__underground {
    font-size: 11px;
  }
}
@media (max-width: 576px) {
  .container {
    width: 400px;
  }
  .header__up {
    flex-direction: column;
    gap: 20px;
  }
  .header__contacts {
    width: 100%;
  }
  .btn_header {
    width: 154px;
  }
  .navigation__dropdown {
    width: 150px;
  }
  .promo__title {
    font-size: 32px;
  }
  .promo__descr {
    font-size: 13px;
  }
  .promo__wrapper {
    flex-direction: column;
    align-items: center;
  }
  .promo__btn {
    width: 90%;
  }
  .advantages__items {
    gap: 30px;
  }
  .steps__title {
    margin-top: 50px;
    font-size: 25px;
  }
  .steps__title span {
    display: block;
  }
  .steps__wrapper {
    margin-top: 20px;
