.services {
    &__title {
        margin-top: 120px;
        color: var(--typography, #212121);
        font-size: 38px;
        font-weight: 700;
    }

    &__descr {
        margin-top: 40px;
        max-width: 671px;
    }
}

.offers {
    margin: 60px 0 60px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.offer {
    background: var(--gray-light, #F4F4F4);
    width: 360px;
    height: 449px;
    padding: 32px 24px 40px 24px;

    &__title {
        color: var(--typography, #212121);
        font-size: 18px;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.18px;
        text-transform: uppercase;
    }

    &__strip {
        width: 312px;
        height: 1px;
        opacity: 0.2;
        background: var(--gray, #333);
    }

    &__descr {
        height: 80px;
        margin-top: 41px;
        color: var(--typography, #212121);
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        opacity: 0.5;
    }

    &__subdescr {
        margin-top: 60px;
        color: var(--typography, #212121);
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        opacity: 0.5;
    }

    &__price {
        margin-top: 20px;
        color: var(--orange, #FF5900);
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;

        span {
            font-size: 18px;
            font-weight: 600;
        }
    }

    &__btn {
        margin-top: 53px;
        width: 312px;
        height: 52px;
        display: flex;
        width: 312px;
        padding: 16px 32px;
        justify-content: center;
        align-items: center;
        border-radius: 100px;
        border: 1px solid var(--orange, #FF5900);
        color: var(--orange, #FF5900);
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
    }

    &__btn:hover {
        outline: none;
        box-shadow: 0 0 0 4px #cbd6ee;
    }
}
