.advantages {
    margin-top: 120px;

    &__title {
        font-size: 38px;
        font-weight: 700;
    }

    &__items {
        margin: 50px 0 120px 0;
        display: flex;
        justify-content: space-between;
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }

    &__item-img {
        width: 70px;
        height: 70px;
    }

    &__item-text {
        width: 250px;
        color: var(--gray, #333);
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
    }
}

.strip {
    height: 1px;
    background: var(--gray, #333);
}
