.contacts {
    &__wrapper {
        margin: 30px 0 30px 0;
    }
}

.contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--typography, #212121);
    font-size: 16px;
    font-weight: 500;

    &__left {
        width: 560px;
    }

    &__title {
        color: var(--typography, #212121);
        font-size: 38px;
        font-weight: 700;
    }

    &__descr {
        margin-top: 12px;
    }

    &__left-wrapper {
        margin-top: 38px;
        display: flex;
        justify-content: space-between;
    }

    &__adress-title {
        color: var(--typography, #212121);
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.18px;
        text-transform: uppercase;
        opacity: 0.2;
    }

    &__adress-descr {
        margin-top: 8px;
        width: 300px;
    }

    &__regime {
        margin-top: 28px;
    }

    &__regime-title {
        color: var(--typography, #212121);
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.18px;
        text-transform: uppercase;
        opacity: .2;
    }

    &__regime-descr {
        margin-top: 8px;
    }

    &__social-title {
        color: var(--typography, #212121);
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.18px;
        text-transform: uppercase;
        opacity: .2;
    }

    &__social-phone {
        margin-top: 8px;
    }

    &__icons {
        margin-top: 28px;
        display: flex;
    }

    &__icons-item:nth-child(2) {
        margin-left: 20px;
    }
}

.map {
    max-width: 600px;
    max-height: 600px;
}
